/**
 * EventBus -     UI   
 * 
 * :
 * 
 * 1.   :
 *    useEffect(() => {
 *      const unsubscribe = eventBus.on('finance:updated', (data) => {
 *        refetchData();
 *      });
 *      return () => unsubscribe();
 *    }, []);
 * 
 * 2.  :
 *    eventBus.emit('finance:updated', { orderId: 123 });
 */

class EventBus {
  constructor() {
    this.events = {};
  }

  /**
   *   
   * @param {string} event -  
   * @param {Function} callback - -
   * @returns {Function} -   
   */
  on(event, callback) {
    if (!this.events[event]) {
      this.events[event] = [];
    }
    this.events[event].push(callback);
    
    //    
    return () => {
      this.events[event] = this.events[event].filter(cb => cb !== callback);
    };
  }

  /**
   *  
   * @param {string} event -  
   * @param {any} data -  
   */
  emit(event, data) {
    if (this.events[event]) {
      this.events[event].forEach(callback => {
        try {
          callback(data);
        } catch (err) {
          console.error(`EventBus error in ${event}:`, err);
        }
      });
    }
  }

  /**
   *     
   * @param {string} event -  
   */
  off(event) {
    delete this.events[event];
  }
}

// Singleton instance
const eventBus = new EventBus();

//  
export const EVENTS = {
  // 
  FINANCE_UPDATED: 'finance:updated',
  PAYMENT_CREATED: 'finance:payment_created',
  DEPOSIT_CREATED: 'finance:deposit_created',
  DEPOSIT_REFUNDED: 'finance:deposit_refunded',
  
  // 
  ORDER_UPDATED: 'order:updated',
  ORDER_STATUS_CHANGED: 'order:status_changed',
  
  // Issue cards
  ISSUE_CARD_UPDATED: 'issue:updated',
  
  //  refetch
  REFETCH_ALL: 'global:refetch',
};

export default eventBus;
