import React, { useState, useEffect } from 'react'
import CorporateHeader from '../components/CorporateHeader'

/************** helpers **************/
const cls = (...a: (string | false | null | undefined)[]) => a.filter(Boolean).join(' ')

/************** types **************/
type OrderStatus = 'progress' | 'ready'

interface PackingOrder {
  id: string
  order_id: string
  client: string
  eventDate: string
  issueTime: string
  returnTime: string
  manager: string
  status: OrderStatus
  itemsCount: number
  skuCount: number
  progressPack: number
  warehouseZone: string
  notes: string
}

/************** small UI **************/
function Badge({ tone = 'slate', children }: { tone?: string; children: React.ReactNode }) {
  const tones: Record<string, string> = {
    slate: 'bg-slate-100 text-slate-700 border-slate-200',
    blue: 'bg-sky-100 text-sky-700 border-sky-200',
    green: 'bg-emerald-100 text-emerald-700 border-emerald-200',
    amber: 'bg-amber-100 text-amber-800 border-amber-200',
    violet: 'bg-violet-100 text-violet-700 border-violet-200',
  }
  return (
    <span
      className={cls(
        'inline-flex items-center gap-1 rounded-full border px-2 py-0.5 text-[10px]',
        tones[tone] || tones.slate,
      )}
    >
      {children}
    </span>
  )
}

function PillButton({
  children,
  onClick,
  tone = 'slate',
}: {
  children: React.ReactNode
  onClick?: () => void
  tone?: string
}) {
  const tones: Record<string, string> = {
    slate: 'bg-slate-900 text-white hover:bg-slate-800',
    green: 'bg-emerald-600 text-white hover:bg-emerald-700',
    ghost: 'bg-transparent text-slate-700 hover:bg-slate-100 border border-slate-200',
  }
  return (
    <button
      type="button"
      onClick={onClick}
      className={cls('rounded-full px-3 py-1 text-[11px] font-medium transition', tones[tone] || tones.slate)}
    >
      {children}
    </button>
  )
}

function ProgressBar({ value }: { value: number }) {
  const v = Math.max(0, Math.min(100, value))
  return (
    <div className="h-1.5 w-full overflow-hidden rounded-full bg-slate-200">
      <div className="h-full rounded-full bg-sky-500" style={{ width: `${v}%` }} />
    </div>
  )
}

/************** packing list (left column) **************/
function PackingList({ 
  orders, 
  selectedId, 
  onSelect,
  loading 
}: { 
  orders: PackingOrder[]
  selectedId: string | null
  onSelect: (id: string) => void
  loading: boolean
}) {
  return (
    <div className="rounded-2xl border border-slate-200 bg-white p-4">
      <div className="mb-3 flex items-center justify-between">
        <div>
          <h2 className="text-sm font-semibold text-corp-text-dark">  </h2>
          <p className="text-[11px] text-corp-text-muted">        progress / ready.</p>
        </div>
        <div className="flex gap-2 text-[11px] text-corp-text-muted">
          <Badge tone="blue"> : {orders.filter((o) => o.status === 'progress').length}</Badge>
          <Badge tone="green">: {orders.filter((o) => o.status === 'ready').length}</Badge>
        </div>
      </div>

      {loading ? (
        <div className="py-8 text-center text-sm text-corp-text-muted">...</div>
      ) : orders.length === 0 ? (
        <div className="py-8 text-center text-sm text-corp-text-muted">   </div>
      ) : (
        <div className="space-y-2 text-xs">
          {orders.map((o) => (
            <button
              key={o.id}
              type="button"
              onClick={() => onSelect(o.id)}
              className={cls(
                'flex w-full items-center justify-between gap-3 rounded-xl border px-3 py-2 text-left transition',
                selectedId === o.id ? 'border-slate-900 bg-slate-900/5' : 'border-slate-200 hover:border-slate-300',
              )}
            >
              <div>
                <div className="flex items-center gap-2">
                  <span className="text-[11px] font-semibold text-corp-text-dark">#{o.order_id}</span>
                  <span className="text-[11px] text-corp-text-muted">{o.client}</span>
                </div>
                <div className="mt-0.5 flex flex-wrap items-center gap-2 text-[10px] text-corp-text-muted">
                  <span>
                    {o.skuCount} SKU  {o.itemsCount} .
                  </span>
                  <span>: {o.warehouseZone}</span>
                </div>
              </div>
              <div className="flex flex-col items-end gap-1 text-[10px]">
                <span className="text-corp-text-muted">: {o.eventDate}</span>
                <span className="text-corp-text-muted">: {o.issueTime}</span>
                <span>
                  <Badge tone={o.status === 'ready' ? 'green' : 'blue'}>
                    {o.status === 'ready' ? '  ' : ''}
                  </Badge>
                </span>
              </div>
            </button>
          ))}
        </div>
      )}
    </div>
  )
}

