/* eslint-disable */
/**
 * OrderItemsModification -      
 *  ,        
 */

import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Plus, Minus, X, RotateCcw, Search, Package, AlertTriangle, History, ChevronDown, ChevronUp } from 'lucide-react'
import { Button } from '../ui/button'
import { Input } from '../ui/input'
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '../ui/dialog'
import { useToast } from '../../hooks/use-toast'

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || ''

export default function OrderItemsModification({ 
  orderId, 
  orderStatus,
  items = [], 
  onItemsChange,
  onTotalsChange 
}) {
  const { toast } = useToast()
  
  //     (order status  issue card status)
  // : preparation -> processing, ready -> ready_for_issue
  const allowedStatuses = ['processing', 'ready_for_issue', 'preparation', 'ready']
  const canModify = allowedStatuses.includes(orderStatus)
  
  // 
  const [loading, setLoading] = useState(false)
  const [showAddModal, setShowAddModal] = useState(false)
  const [showHistoryModal, setShowHistoryModal] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [searchResults, setSearchResults] = useState([])
  const [searching, setSearching] = useState(false)
  const [modifications, setModifications] = useState([])
  const [refusedItems, setRefusedItems] = useState([])
  const [showRefused, setShowRefused] = useState(false)
  const [selectedQuantities, setSelectedQuantities] = useState({}) //     
  
  //    
  useEffect(() => {
    if (orderId) {
      loadModifications()
      loadRefusedItems()
    }
  }, [orderId])
  
  const loadModifications = async () => {
    try {
      const token = localStorage.getItem('token')
      const res = await axios.get(`${BACKEND_URL}/api/orders/${orderId}/modifications`, {
        headers: { Authorization: `Bearer ${token}` }
      })
      setModifications(res.data.modifications || [])
    } catch (err) {
      console.log('Could not load modifications:', err)
    }
  }
  
  const loadRefusedItems = async () => {
    try {
      const token = localStorage.getItem('token')
      const res = await axios.get(`${BACKEND_URL}/api/orders/${orderId}/items/refused`, {
        headers: { Authorization: `Bearer ${token}` }
      })
      setRefusedItems(res.data.refused_items || [])
    } catch (err) {
      console.log('Could not load refused items:', err)
    }
  }
  
  //   -    API      
  const searchProducts = async (query) => {
    if (!query || query.length < 2) {
      setSearchResults([])
      return
    }
    
    setSearching(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.get(`${BACKEND_URL}/api/orders/inventory/search`, {
        params: { query, limit: 20 },
        headers: { Authorization: `Bearer ${token}` }
      })
      
      //  
      const results = (res.data.products || []).map(p => ({
        product_id: p.product_id,
        sku: p.sku,
        name: p.name,
        rental_price: p.rent_price || 0,
        price: p.price || 0,
        quantity: p.available_quantity || 0,
        image_url: p.image_url
      }))
      
      setSearchResults(results)
    } catch (err) {
      console.error('Search error:', err)
      setSearchResults([])
    } finally {
      setSearching(false)
    }
  }
  
  //     
  const handleAddItem = async (product) => {
    const quantity = selectedQuantities[product.product_id] || 1
    
    //  
    if (quantity > product.quantity) {
      toast({
        title: '  ',
        description: `   ${product.quantity} `,
        variant: 'destructive'
      })
      return
    }
    
    setLoading(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.post(
        `${BACKEND_URL}/api/orders/${orderId}/items`,
        {
          product_id: product.product_id,
          quantity: quantity,
          note: `: ${quantity} `
        },
        { headers: { Authorization: `Bearer ${token}` } }
      )
      
      toast({
        title: '  ',
        description: `${product.name} (${quantity} )   `
      })
      
      //  
      if (onTotalsChange && res.data.totals) {
        onTotalsChange(res.data.totals)
      }
      
      //  
      setShowAddModal(false)
      setSearchQuery('')
      setSearchResults([])
      setSelectedQuantities({})
      
      //      items
      window.location.reload()
      
    } catch (err) {
      toast({
        title: ' ',
        description: err.response?.data?.detail || '   ',
        variant: 'destructive'
      })
    } finally {
      setLoading(false)
    }
  }
  
  //  
  const handleUpdateQuantity = async (itemId, newQuantity) => {
    if (newQuantity < 0) return
    
    setLoading(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.patch(
        `${BACKEND_URL}/api/orders/${orderId}/items/${itemId}`,
        {
          quantity: newQuantity,
          note: ` `
        },
        { headers: { Authorization: `Bearer ${token}` } }
      )
      
      toast({
        title: '  ',
        description: res.data.message
      })
      
      if (onTotalsChange && res.data.totals) {
        onTotalsChange(res.data.totals)
      }
      
      loadModifications()
      
      //    items
      if (onItemsChange) {
        onItemsChange(items.map(item => 
          item.id === itemId ? { ...item, qty: newQuantity } : item
        ))
      }
      
    } catch (err) {
      toast({
        title: ' ',
        description: err.response?.data?.detail || '   ',
        variant: 'destructive'
      })
    } finally {
      setLoading(false)
    }
  }
  
  //  
  const handleRemoveItem = async (itemId, productName) => {
    if (!confirm(`  "${productName}"?`)) return
    
    setLoading(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.delete(
        `${BACKEND_URL}/api/orders/${orderId}/items/${itemId}`,
        { 
          headers: { Authorization: `Bearer ${token}` },
          data: { reason: ' ' }
        }
      )
      
      toast({
        title: '  ',
        description: `${productName}   `
      })
      
      if (onTotalsChange && res.data.totals) {
        onTotalsChange(res.data.totals)
      }
      
      loadModifications()
      loadRefusedItems()
      
      //    -  item
      if (onItemsChange) {
        onItemsChange(items.filter(item => item.id !== itemId))
      }
      
    } catch (err) {
      toast({
        title: ' ',
        description: err.response?.data?.detail || '   ',
        variant: 'destructive'
      })
    } finally {
      setLoading(false)
    }
  }
  
  //   
  const handleRestoreItem = async (itemId, productName) => {
    setLoading(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.post(
        `${BACKEND_URL}/api/orders/${orderId}/items/${itemId}/restore`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      )
      
      toast({
        title: '  ',
        description: `${productName}   `
      })
      
      if (onTotalsChange && res.data.totals) {
        onTotalsChange(res.data.totals)
      }
      
      loadModifications()
      loadRefusedItems()
      
      //  
      window.location.reload()
      
    } catch (err) {
      toast({
        title: ' ',
        description: err.response?.data?.detail || '   ',
        variant: 'destructive'
      })
    } finally {
      setLoading(false)
    }
  }
  
  if (!canModify) {
    return null //      
  }
  
  return (
    <div className="space-y-3">
      {/*   */}
      <div className="flex items-center gap-2 p-2 bg-amber-50 border border-amber-200 rounded-lg">
        <Package className="h-4 w-4 text-amber-600" />
        <span className="text-sm font-medium text-amber-800"></span>
        <div className="flex-1" />
        
        <Button
          size="sm"
          variant="outline"
          onClick={() => setShowAddModal(true)}
          disabled={loading}
          className="gap-1"
        >
          <Plus className="h-3 w-3" />
           
        </Button>
        
        {modifications.length > 0 && (
          <Button
            size="sm"
            variant="ghost"
            onClick={() => setShowHistoryModal(true)}
            className="gap-1"
          >
            <History className="h-3 w-3" />
             ({modifications.length})
          </Button>
        )}
      </div>
      
      {/*   */}
      {refusedItems.length > 0 && (
        <div className="border border-red-200 rounded-lg overflow-hidden">
          <button
            onClick={() => setShowRefused(!showRefused)}
            className="w-full flex items-center gap-2 p-2 bg-red-50 hover:bg-red-100 transition-colors"
          >
            <AlertTriangle className="h-4 w-4 text-red-500" />
            <span className="text-sm font-medium text-red-700">
                ({refusedItems.length})
            </span>
            <div className="flex-1" />
            {showRefused ? <ChevronUp className="h-4 w-4" /> : <ChevronDown className="h-4 w-4" />}
          </button>
          
          {showRefused && (
            <div className="p-2 space-y-2">
              {refusedItems.map(item => (
                <div key={item.id} className="flex items-center gap-2 p-2 bg-red-50/50 rounded">
                  <div className="flex-1">
                    <div className="text-sm font-medium">{item.product_name}</div>
                    <div className="text-xs text-gray-500">
                      {item.original_quantity || item.quantity}   {item.refusal_reason || ' '}
                    </div>
                  </div>
                  <Button
                    size="sm"
                    variant="ghost"
                    onClick={() => handleRestoreItem(item.id, item.product_name)}
                    disabled={loading}
                    className="gap-1 text-green-600 hover:text-green-700"
                  >
                    <RotateCcw className="h-3 w-3" />
                    
                  </Button>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
      
      {/*    */}
      <Dialog open={showAddModal} onOpenChange={setShowAddModal}>
        <DialogContent className="max-w-lg">
          <DialogHeader>
            <DialogTitle>   </DialogTitle>
          </DialogHeader>
          
          <div className="space-y-4">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input
                placeholder="    SKU..."
                value={searchQuery}
                onChange={(e) => {
                  setSearchQuery(e.target.value)
                  searchProducts(e.target.value)
                }}
                className="pl-10"
              />
            </div>
            
            <div className="max-h-[400px] overflow-y-auto space-y-2">
              {searching && (
                <div className="text-center text-gray-500 py-4">...</div>
              )}
              
              {!searching && searchResults.length === 0 && searchQuery.length >= 2 && (
                <div className="text-center text-gray-500 py-4">  </div>
              )}
              
              {searchResults.map(product => {
                const qty = selectedQuantities[product.product_id] || 1
                const available = product.quantity || 0
                
                return (
                  <div
                    key={product.product_id}
                    className="flex items-center gap-3 p-3 border rounded-lg hover:bg-gray-50"
                  >
                    {product.image_url ? (
                      <img 
                        src={product.image_url} 
                        alt={product.name}
                        className="w-14 h-14 object-cover rounded"
                      />
                    ) : (
                      <div className="w-14 h-14 bg-gray-100 rounded flex items-center justify-center">
                        <Package className="h-6 w-6 text-gray-400" />
                      </div>
                    )}
                    
                    <div className="flex-1">
                      <div className="font-medium text-sm">{product.name}</div>
                      <div className="text-xs text-gray-500">
                        {product.sku}  <span className={available > 0 ? 'text-green-600' : 'text-red-500'}>{available}   </span>
                      </div>
                      <div className="text-xs text-gray-400">
                        {product.rental_price || 0} /  : {product.price || 0} 
                      </div>
                    </div>
                    
                    {/*   */}
                    <div className="flex items-center gap-2">
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={(e) => {
                          e.stopPropagation()
                          setSelectedQuantities(prev => ({
                            ...prev,
                            [product.product_id]: Math.max(1, qty - 1)
                          }))
                        }}
                        disabled={qty <= 1}
                        className="h-8 w-8 p-0"
                      >
                        <Minus className="h-3 w-3" />
                      </Button>
                      
                      <span className="w-8 text-center font-medium">{qty}</span>
                      
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={(e) => {
                          e.stopPropagation()
                          setSelectedQuantities(prev => ({
                            ...prev,
                            [product.product_id]: Math.min(available, qty + 1)
                          }))
                        }}
                        disabled={qty >= available}
                        className="h-8 w-8 p-0"
                      >
                        <Plus className="h-3 w-3" />
                      </Button>
                    </div>
                    
                    {/*   */}
                    <Button
                      size="sm"
                      onClick={() => handleAddItem(product)}
                      disabled={loading || available === 0}
                      className="gap-1 bg-green-600 hover:bg-green-700"
                    >
                      <Plus className="h-3 w-3" />
                      
                    </Button>
                  </div>
                )
              })}
            </div>
          </div>
          
          <DialogFooter>
            <Button variant="outline" onClick={() => {
              setShowAddModal(false)
              setSelectedQuantities({})
            }}>
              
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      
      {/*    */}
      <Dialog open={showHistoryModal} onOpenChange={setShowHistoryModal}>
        <DialogContent className="max-w-lg">
          <DialogHeader>
            <DialogTitle>  </DialogTitle>
          </DialogHeader>
          
          <div className="max-h-[400px] overflow-y-auto space-y-2">
            {modifications.map(mod => (
              <div 
                key={mod.id} 
                className={`p-3 rounded-lg border ${
                  mod.modification_type === 'add' ? 'bg-green-50 border-green-200' :
                  mod.modification_type === 'update' ? 'bg-amber-50 border-amber-200' :
                  'bg-red-50 border-red-200'
                }`}
              >
                <div className="flex items-center gap-2">
                  {mod.modification_type === 'add' && <Plus className="h-4 w-4 text-green-600" />}
                  {mod.modification_type === 'update' && <Package className="h-4 w-4 text-amber-600" />}
                  {mod.modification_type === 'remove' && <X className="h-4 w-4 text-red-600" />}
                  <span className="font-medium text-sm">{mod.product_name}</span>
                </div>
                <div className="mt-1 text-xs text-gray-600">
                  {mod.modification_type === 'add' && `: ${mod.new_quantity} `}
                  {mod.modification_type === 'update' && `${mod.old_quantity}  ${mod.new_quantity} `}
                  {mod.modification_type === 'remove' && `: ${mod.old_quantity} `}
                </div>
                <div className="mt-1 text-xs text-gray-500 flex justify-between">
                  <span>{mod.reason}</span>
                  <span>{mod.created_by}  {mod.created_at}</span>
                </div>
                <div className="mt-1 text-xs">
                  <span className={mod.price_change > 0 ? 'text-green-600' : mod.price_change < 0 ? 'text-red-600' : ''}>
                    : {mod.price_change > 0 ? '+' : ''}{mod.price_change?.toFixed(2)} 
                  </span>
                  <span className="mx-2">|</span>
                  <span className={mod.deposit_change > 0 ? 'text-green-600' : mod.deposit_change < 0 ? 'text-red-600' : ''}>
                    : {mod.deposit_change > 0 ? '+' : ''}{mod.deposit_change?.toFixed(2)} 
                  </span>
                </div>
              </div>
            ))}
            
            {modifications.length === 0 && (
              <div className="text-center text-gray-500 py-8"> </div>
            )}
          </div>
          
          <DialogFooter>
            <Button variant="outline" onClick={() => setShowHistoryModal(false)}>
              
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}

//       
export function ItemModificationControls({ item, orderId, orderStatus, onUpdate }) {
  const { toast } = useToast()
  const [loading, setLoading] = useState(false)
  
  const allowedStatuses = ['processing', 'ready_for_issue', 'preparation', 'ready']
  const canModify = allowedStatuses.includes(orderStatus)
  
  if (!canModify) return null
  
  const handleQuantityChange = async (delta) => {
    const newQty = (item.qty || 0) + delta
    if (newQty < 0) return
    
    setLoading(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.patch(
        `${BACKEND_URL}/api/orders/${orderId}/items/${item.id}`,
        {
          quantity: newQty,
          note: delta > 0 ? ' ' : ' '
        },
        { headers: { Authorization: `Bearer ${token}` } }
      )
      
      toast({
        title: '  ',
        description: `${item.qty}  ${newQty}`
      })
      
      if (onUpdate) onUpdate(res.data.totals)
      
    } catch (err) {
      toast({
        title: ' ',
        description: err.response?.data?.detail || '  ',
        variant: 'destructive'
      })
    } finally {
      setLoading(false)
    }
  }
  
  const handleRemove = async () => {
    if (!confirm(` "${item.name}"?`)) return
    
    setLoading(true)
    try {
      const token = localStorage.getItem('token')
      const res = await axios.delete(
        `${BACKEND_URL}/api/orders/${orderId}/items/${item.id}`,
        { 
          headers: { Authorization: `Bearer ${token}` },
          data: { reason: ' ' }
        }
      )
      
      toast({
        title: '  ',
        description: item.name
      })
      
      if (onUpdate) onUpdate(res.data.totals)
      
    } catch (err) {
      toast({
        title: ' ',
        description: err.response?.data?.detail || '  ',
        variant: 'destructive'
      })
    } finally {
      setLoading(false)
    }
  }
  
  return (
    <div className="flex items-center gap-1">
      <Button
        size="sm"
        variant="ghost"
        onClick={() => handleQuantityChange(-1)}
        disabled={loading || (item.qty || 0) <= 0}
        className="h-6 w-6 p-0"
      >
        <Minus className="h-3 w-3" />
      </Button>
      
      <span className="w-6 text-center text-sm font-medium">{item.qty || 0}</span>
      
      <Button
        size="sm"
        variant="ghost"
        onClick={() => handleQuantityChange(1)}
        disabled={loading}
        className="h-6 w-6 p-0"
      >
        <Plus className="h-3 w-3" />
      </Button>
      
      <Button
        size="sm"
        variant="ghost"
        onClick={handleRemove}
        disabled={loading}
        className="h-6 w-6 p-0 text-red-500 hover:text-red-700 ml-2"
        title=" "
      >
        <X className="h-3 w-3" />
      </Button>
    </div>
  )
}
